import React from 'react'
import { Row, Col, Button, Input, Label } from 'reactstrap'
import axios from 'axios'
import Select from 'react-select'
import '../../../assets/scss/plugins/extensions/dropzone.scss'

const contactUsOptions = [
  {
    value: 'Query',
    label: 'Query',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Complain',
    label: 'Complain',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Feedback',
    label: 'Feedback',
    color: '#00B8D9',
    isFixed: true
  }
]
const user = JSON.parse(localStorage.getItem('logInUserData'))

class UserAccountTab extends React.Component {
  state = {
    saveButtonText: 'Save',
    contactUsFormType: '',
    contactUsDescription: '',
    user: user,
    mobile: user?.mobile,
    email: user?.email
  }

  postContactUsForm = (e) => {
    e.preventDefault()
    this.setState({ saveButtonText: 'Saving ...' })
    let { firstname, middlename, lastname, mobile } = this.state
    user.salutation = this.state.salutation
    user.name = firstname + ' ' + middlename + ' ' + lastname
    user.mobile = mobile
    localStorage.setItem('logInUserData', JSON.stringify(user))
    axios
      .post('/backendapi/adddetails', user, {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('authtoken')
        }
      })
      .then((res) => {
        window.location.reload()
      })
      .catch()
  }

  render() {
    return (
      <>
        <Row xs="1">
          <Label for="optionSelect">Select a topic *:</Label>
          <Select
            id="optionSelect"
            className="React mt-50"
            classNamePrefix="select"
            ref={this.state.selectAssetRef}
            name="color"
            options={contactUsOptions}
            isClearable={true}
            placeholder="Select a topic..."
            onChange={(e) => {
              this.setState(
                {
                  contactUsFormType: e ? e.value : ''
                },
                this.saveContactUsFormValidation
              )
            }}
          />
        </Row>
        <Row>
          <Label for="description" className="mt-2">
            Description *
          </Label>
          <Input
            className="mt-50"
            type="textarea"
            placeholder="Description"
            rows="5"
            id="description"
            onChange={(e) => {
              this.setState(
                {
                  contactUsDescription: e.target.value
                },
                this.saveContactUsFormValidation
              )
            }}
          />
        </Row>
        <Row>
          <Label for="attachment" className="mt-2">
            Attachments
          </Label>
          <Input
            className="mt-50"
            type="File"
            placeholder="Attachments"
            rows="5"
            id="attachment"
          />
        </Row>

        <Col className="d-flex justify-content-end flex-wrap mt-2" sm="12">
          <Button.Ripple
            color="warning"
            disabled={
              !(
                this.state.contactUsDescription.length &&
                this.state.contactUsFormType.length
              )
            }
            className="button-label"
            onClick={(e) => {
              this.postContactUsForm(e)
            }}
          >
            {this.state.saveButtonText}
          </Button.Ripple>
        </Col>
      </>
    )
  }
}
export default UserAccountTab
