import React from 'react'
import {
  Row,
  Col,
  Button,
  Form,
  Input,
  Label,
  FormGroup,
  Media,
  Modal,
  ModalBody,
  ModalFooter
} from 'reactstrap'
import Checkbox from '../../../components/@vuexy/checkbox/CheckboxesVuexy'
import Radio from '../../../components/@vuexy/radio/RadioVuexy'
import { Settings, FilePlus, Edit, Check } from 'react-feather'
import avatar1 from '../../../assets/img/avatar/female.png'
import avatar2 from '../../../assets/img/avatar/male.png'
import Select from 'react-select'
import Flatpickr from 'react-flatpickr'
import axios from 'axios'
import CreatableSelect from 'react-select/creatable'

import 'flatpickr/dist/themes/light.css'
// import '../../../assets/scss/plugins/forms/flatpickr/flatpickr.scss'

const languages = [
  { value: 'english', label: 'English', color: '#7367f0' },
  { value: 'french', label: 'French', color: '#7367f0' },
  { value: 'german', label: 'German', color: '#7367f0' },
  { value: 'portuguese', label: 'Portuguese', color: '#7367f0' },
  { value: 'arabi', label: 'Arabi', color: '#7367f0' },
  { value: 'chinese', label: 'Chinese', color: '#7367f0' },
  { value: 'hindi', label: 'Hindi', color: '#7367f0' },
  { value: 'russian', label: 'Russian', color: '#7367f0' },
  { value: 'spanish', label: 'Spanish', color: '#7367f0' },
  { value: 'urdu', label: 'Urdu', color: '#7367f0' }
]

const prefixOptions = [
  { value: 'Mr', label: 'Mr', isFixed: false },
  { value: 'Miss', label: 'Miss', isFixed: true },
  { value: 'Mrs', label: 'Mrs', isFixed: true },
  { value: 'Dr', label: 'Dr', isFixed: false }
]

const user = JSON.parse(localStorage.getItem('logInUserData'))
class UserInfoTab extends React.Component {
  state = {
    saveButton: 'Save Changes',
    user: user,
    dob: user?.dob,
    secondarymobile: user?.secondarymobile,
    secondaryemail: user?.secondaryemail,
    address: user?.address,
    theme: user?.theme,
    language: user?.language,
    pwdtime: user?.pwdtime,
    postalCode: user?.address && user?.address.postalCode,
    address1: user?.address && user?.address.address1,
    address2: user?.address && user?.address.address2,
    city: user?.address && user?.address.city,
    state: user?.address && user?.address.state,
    country: user?.address && user?.address.country,
    firstname: '',
    salutation: user?.salutation ? user?.salutation : 'Mr',
    mobile: user?.mobile,
    email: user?.email,
    hidden: true,
    detailsPresent: false,
    infoModal: false
  }

  componentDidMount() {
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    let names = user?.name ? user?.name.split(' ') : user?.username.split(' ')
    let firstname = ''
    let middlename = ''
    let lastname = ''
    if (names?.length === 1) {
      firstname = names[0]
    } else if (names?.length === 2) {
      firstname = names[0]
      lastname = names[1]
    }
    if (names?.length === 3) {
      firstname = names[0]
      lastname = names[2]
      middlename = names[1]
    }
    if (names?.length > 3) {
      firstname = names[0]
      for (let i = 1; i < names?.length - 1; i = i + 1) {
        middlename = middlename + ' ' + names[i]
      }
      lastname = names[2]
      middlename = names[names?.length - 1]
    }
    this.setState({
      firstname,
      middlename,
      lastname,
      emailPrimary: user?.email,
      emailSecondary: user?.secondaryemail ? user?.secondaryemail : ''
    })

    if (
      user?.address?.address1 &&
      user?.address?.address2 &&
      user?.address?.city &&
      user?.address?.country &&
      user?.address?.postalCode &&
      user?.address?.state &&
      user?.language &&
      user?.theme &&
      user?.pwdtime &&
      user?.communication.length > 0
    ) {
      this.setState({ detailsPresent: true })
    }
  }

  handledob = (date) => {
    this.setState({
      dob: date
    })
  }

  updateuser = (e) => {
    e.preventDefault()
    this.setState({ saveButton: 'Saving ...' })
    const {
      dob,
      language,
      secondarymobile,
      secondaryemail,
      pwdtime,
      theme,
      address1,
      address2,
      postalCode,
      city,
      state,
      country,
      firstname,
      middlename,
      lastname,
      mobile,
      salutation
    } = this.state
    user.dob = dob
    user.pwdtime = pwdtime
    user.theme = theme
    user.secondarymobile = secondarymobile
    user.secondaryemail = secondaryemail
    user.language = language
    user.salutation = salutation
    user.name = firstname + ' ' + middlename + ' ' + lastname
    user.mobile = mobile
    const add = {
      address1,
      address2,
      postalCode,
      city,
      state,
      country
    }
    user.address = add
    localStorage.setItem('logInUserData', JSON.stringify(user))
    localStorage.setItem('theme', theme)
    axios
      .post(
        '/backendapi/adddetails',
        { _id: user._id, user },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('authtoken')
          }
        }
      )
      .then((res) => {
        this.setState({ saveButton: 'Save Changes' })
        window.location.reload()
      })
      .catch()
  }

  pwdtimeChange = (e, i) => {
    console.log(e, i)
    this.setState({ pwdtime: i })
  }

  animateLabel = (value) => {
    this.setState({ hidden: !this.state.hidden })
  }

  openInfoModal = (value) => {
    this.setState({ infoModal: value })
  }

  render() {
    const { detailsPresent } = this.state
    return (
      <>
        <Modal
          isOpen={this.state.infoModal}
          className="modal-dialog-centered"
          fade={true}
          toggle={() => this.openInfoModal(false)}
          style={{ maxWidth: '800px' }}
          backdrop="static"
        >
          <ModalBody>
            <Col
              sm="12"
              className={window.screen.width <= 500 ? 'd-block' : 'd-flex'}
            >
              <Col md={window.screen.width <= 500 ? '12' : '6'}>
                <Row xs="1">
                  <FormGroup>
                    <Label for="address1">Address Line 1</Label>
                    <Input
                      type="text"
                      id="address1" //placeholder="Address Line 1"
                      value={this.state.address1}
                      onChange={(e) =>
                        this.setState({ address1: e.target.value })
                      }
                    />
                  </FormGroup>
                </Row>
                <Row xs="1">
                  <FormGroup>
                    <Label for="address1">Address Line 2</Label>
                    <Input
                      type="text"
                      id="address1" //placeholder="Address Line 2"
                      value={this.state.address2}
                      onChange={(e) =>
                        this.setState({ address2: e.target.value })
                      }
                    />
                  </FormGroup>
                </Row>
                <Row xs="1">
                  <FormGroup>
                    <Label for="pincode">Pincode</Label>
                    <Input
                      type="number"
                      id="pincode" //placeholder="Pincode"
                      value={this.state.postalCode}
                      onChange={(e) =>
                        this.setState({ postalCode: e.target.value })
                      }
                    />
                  </FormGroup>
                </Row>
                <Row xs="1">
                  <FormGroup>
                    <Label for="city">City</Label>
                    <Input
                      type="text"
                      //defaultValue="Camden Town"
                      id="city"
                      //placeholder="City"
                      value={this.state.city}
                      onChange={(e) => this.setState({ city: e.target.value })}
                    />
                  </FormGroup>
                </Row>
                <Row xs="1">
                  <FormGroup>
                    <Label for="State">State</Label>
                    <Input
                      type="text"
                      //defaultValue="London"
                      id="State"
                      //placeholder="State"
                      value={this.state.state}
                      onChange={(e) => this.setState({ state: e.target.value })}
                    />
                  </FormGroup>
                </Row>
                <Row xs="1">
                  <FormGroup>
                    <Label for="Country">Country</Label>
                    <Input
                      type="text"
                      //defaultValue="UK"
                      id="Country"
                      //placeholder="Country"
                      value={this.state.country}
                      onChange={(e) =>
                        this.setState({ country: e.target.value })
                      }
                    />
                  </FormGroup>
                </Row>
              </Col>
              <Col
                md={window.screen.width <= 500 ? '12' : '6'}
                sm={{ size: 'auto', offset: 1 }}
              >
                <Row>
                  <FormGroup style={{ marginBottom: '1rem' }}>
                    <Label className="d-block mb-50" for="communication">
                      Communication
                    </Label>
                    <div className="d-inline-block mr-1">
                      <Checkbox
                        color="primary"
                        icon={<Check className="vx-icon" size={16} />}
                        label="Email"
                        disabled={true}
                        defaultChecked={true}
                      />
                    </div>
                    <br />{' '}
                    <div className="d-inline-block mr-1">
                      <Checkbox
                        color="primary"
                        icon={<Check className="vx-icon" size={16} />}
                        label="SMS"
                        disabled={true}
                        defaultChecked={true}
                      />
                    </div>
                    <br />
                    <div className="d-inline-block">
                      <Checkbox
                        color="primary"
                        icon={<Check className="vx-icon" size={16} />}
                        label="Phone"
                        disabled={true}
                        defaultChecked={true}
                      />
                    </div>
                  </FormGroup>
                </Row>
                <Row>
                  <FormGroup style={{ marginBottom: '1rem' }}>
                    <Label className="d-block mb-50">Theme</Label>
                    <div className="d-inline-block mr-1">
                      <Radio
                        label="Dark"
                        color="primary"
                        defaultChecked={
                          this.state.user?.theme === 'dark' ? true : false
                        }
                        name="theme"
                        onChange={(e) => {
                          this.setState({ theme: 'dark' })
                        }}
                      />
                    </div>
                    <br />
                    <div className="d-inline-block mr-1">
                      <Radio
                        label="Light"
                        color="primary"
                        defaultChecked={
                          this.state.user?.theme === 'dark' ? false : true
                        }
                        name="theme"
                        onChange={(e) => {
                          this.setState({ theme: 'light' })
                        }}
                      />
                    </div>
                  </FormGroup>
                </Row>
                <Row>
                  <FormGroup style={{ marginBottom: '1rem' }}>
                    <Label className="d-block mb-50">Gender</Label>
                    <div className="d-inline-block mr-1">
                      <Radio
                        label="Male"
                        color="primary"
                        defaultChecked={
                          this.state.user?.salutation === 'Mr' ? true : false
                        }
                        name="gender"
                        disabled={true}
                      />
                    </div>
                    <br />
                    <div className="d-inline-block mr-1">
                      <Radio
                        label="Female"
                        color="primary"
                        defaultChecked={
                          this.state.user?.salutation === 'Mr' ? false : true
                        }
                        name="gender"
                        disabled={true}
                      />
                    </div>
                    <br />
                    <div className="d-inline-block">
                      <Radio
                        label="Others"
                        color="primary"
                        defaultChecked={false}
                        name="gender"
                        disabled={true}
                      />
                    </div>
                  </FormGroup>
                </Row>
                <Row>
                  <FormGroup style={{ marginBottom: '1rem' }}>
                    <Label className="d-block mb-50">
                      Password Show Time in Password Vault
                    </Label>
                    <div className="d-inline-block mr-1">
                      <Radio
                        label="3 Seconds"
                        color="primary"
                        defaultChecked={
                          this.state.pwdtime === 3
                            ? true
                            : this.state.pwdtime
                            ? false
                            : true
                        }
                        name="pwd"
                        onChange={(e) => {
                          this.pwdtimeChange(e, 3)
                        }}
                      />
                    </div>
                    <br />
                    <div className="d-inline-block mr-1">
                      <Radio
                        label="5 Seconds"
                        color="primary"
                        defaultChecked={this.state.pwdtime === 5 ? true : false}
                        name="pwd"
                        onChange={(e) => {
                          this.pwdtimeChange(e, 5)
                        }}
                      />
                    </div>
                    <br />
                    <div className="d-inline-block">
                      <Radio
                        label="10 Seconds"
                        color="primary"
                        defaultChecked={
                          this.state.pwdtime === 10 ? true : false
                        }
                        name="pwd"
                        onChange={(e) => {
                          this.pwdtimeChange(e, 10)
                        }}
                      />
                    </div>
                  </FormGroup>
                </Row>
              </Col>
            </Col>
          </ModalBody>
          <ModalFooter className="justify-content-center">
            <Button color="warning" onClick={() => this.openInfoModal(false)}>
              OK
            </Button>
          </ModalFooter>
        </Modal>
        <Form onSubmit={(e) => e.preventDefault()}>
          <Col className="mt-1">
            <Row className="mt-1" sm="12">
              <Media className="mb-2 ml-1">
                <Media className="mr-2 my-25" left href="#">
                  <Media
                    className="users-avatar-shadow rounded"
                    object
                    src={
                      JSON.parse(localStorage.getItem('logInUserData')) &&
                      JSON.parse(localStorage.getItem('logInUserData'))
                        .salutation === 'Mr'
                        ? avatar2
                        : avatar1
                    }
                    alt="user profile image"
                    height="84"
                    width="84"
                  />
                </Media>
                <Media className="mt-2" body>
                  <Media
                    className="font-medium-1 text-bold-600"
                    tag="p"
                    heading
                  >
                    {this.state.user?.name}
                  </Media>
                  {/* <div className="d-flex flex-wrap">
                <Button.Ripple className="mr-1" color="primary" outline>
                  Change
                </Button.Ripple>
                <Button.Ripple color="flat-danger">Remove Avatar</Button.Ripple>
              </div> */}
                </Media>
              </Media>
            </Row>
            <Row sm="">
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label>Salutaion</Label>
                  <CreatableSelect
                    isMulti={false}
                    isClearable={false}
                    options={prefixOptions}
                    className="React"
                    classNamePrefix="select"
                    //defaultValue={prefixOptions[0]}
                    value={{
                      value: this.state.salutation,
                      label: this.state.salutation,
                      isFixed: false
                    }}
                    onChange={(e) =>
                      this.setState({
                        salutation: e && e.value ? e.value : null
                      })
                    }
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label>First Name</Label>
                  <Input
                    type="text"
                    //placeholder="First Name"
                    required
                    value={this.state.firstname}
                    onChange={(e) =>
                      this.setState({ firstname: e.target.value })
                    }
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label>Middle Name</Label>
                  <Input
                    type="text"
                    //placeholder="Middle Name"
                    //required
                    value={this.state.middlename}
                    onChange={(e) =>
                      this.setState({ middlename: e.target.value })
                    }
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label>Last Name</Label>
                  <Input
                    type="text"
                    //placeholder="Last Name"
                    required
                    value={this.state.lastname}
                    onChange={(e) =>
                      this.setState({ lastname: e.target.value })
                    }
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label for="languages">Languages</Label>
                  <Select
                    languages
                    //isMulti
                    defaultValue={languages.filter((e) => {
                      return e.value === this.state.language
                    })}
                    isClearable={true}
                    //styles={colourStyles}
                    options={languages}
                    className="React"
                    classNamePrefix="select"
                    id="languages"
                    onChange={(e) => {
                      this.setState({ language: e ? e.value : undefined })
                    }}
                  />
                </FormGroup>
              </Col>
              {/* </Row>
            <Row sm="12"> */}
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label for="dob">Date of birth</Label>
                  <Flatpickr
                    id="dob"
                    className="form-control"
                    options={{ dateFormat: 'Y-m-d' }}
                    value={this.state.dob}
                    onChange={(date) => this.handledob(date)}
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '4'
                }
              >
                <FormGroup>
                  <Label>Primary Email</Label>
                  <Input
                    type="email"
                    //placeholder="Primary Email"
                    required
                    disabled={true}
                    value={this.state.emailPrimary}
                    // onChange={(e) =>
                    //   this.setState({ emailPrimary: e.target.value })
                    // }
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '4'
                }
              >
                <FormGroup>
                  <Label> Secondary Email</Label>
                  <Input
                    type="email"
                    //placeholder="Primary Contact"
                    required
                    value={this.state.secondaryemail}
                    onChange={(e) =>
                      this.setState({
                        secondaryemail: e.target.value
                      })
                    }
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label> Primary Contact</Label>
                  <Input
                    type="emailSecondary"
                    // placeholder="Secondary Email"
                    required
                    value={this.state.mobile}
                    onChange={(e) =>
                      this.setState({
                        mobile: e.target.value
                      })
                    }
                  />
                </FormGroup>
              </Col>
              <Col
                md={
                  window.screen.width <= 500
                    ? '12'
                    : window.screen.width >= 768 && window.screen.width <= 1024
                    ? '3'
                    : '2'
                }
              >
                <FormGroup>
                  <Label> Secondary Contact</Label>

                  <Input
                    type="number"
                    //placeholder="Secondary Contact"
                    required
                    value={this.state.secondarymobile}
                    onChange={(e) =>
                      this.setState({
                        secondarymobile: e.target.value
                      })
                    }
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row
              sm="12"
              className="d-flex justify-content-between flex-wrap"
              style={{ position: 'relative', bottom: '-5rem' }}
            >
              <Col md="4" className="d-flex">
                <Button
                  className="add-button"
                  color="warning"
                  onMouseEnter={() => {
                    this.animateLabel()
                  }}
                  style={{ height: '45px', width: '45px' }}
                >
                  <Settings size="25" style={{ margin: '8px' }} />
                </Button>

                <div
                  className="add-info-div"
                  style={{ width: !detailsPresent ? '85px' : '110px' }}
                >
                  <div
                    className={this.state.hidden ? 'no-display' : 'button-text'}
                  >
                    {!detailsPresent ? (
                      <FilePlus size="18" />
                    ) : (
                      <Edit size="18" />
                    )}
                    <label
                      onClick={() => this.openInfoModal(true)}
                      style={{
                        fontSize: '14px',
                        fontWeight: '600',
                        position: 'relative',
                        top: '3px',
                        cursor: 'pointer'
                      }}
                    >
                      {!detailsPresent ? 'Add Info' : 'Update Info'}
                    </label>
                  </div>
                </div>
              </Col>
              <Col className="d-flex justify-content-end flex-wrap" md="8">
                <Button.Ripple
                  color="flat-warning"
                  style={{ width: 'max-content' }}
                >
                  Reset
                </Button.Ripple>
                <Button.Ripple
                  className=""
                  color="primary"
                  onClick={(e) => {
                    this.updateuser(e)
                  }}
                  style={{ width: 'max-content' }}
                >
                  {this.state.saveButton}
                </Button.Ripple>
              </Col>
            </Row>
          </Col>
        </Form>
      </>
    )
  }
}
export default UserInfoTab
